import React from "react";
import NProgress from "nprogress";
import styled from "styled-components";

import MuffynLogoSvg from "../components/MuffynLogoSvg";
import Title from "../components/Title";
import Button from "../components/Button";

const Container = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
`;

const StyledP = styled.p`
  line-height: 1.4em;
  max-width: 300px;
`;

const Cta = styled.footer`
  margin-top: 3em;
`;

class CreateAvatarPage extends React.Component {
  uploadFile = async e => {
    const files = e.target.files;
    const data = new FormData();
    data.append("file", files[0]);
    data.append("upload_preset", "sickfits");

    const res = await fetch(
      "https://api.cloudinary.com/v1_1/wesbostutorial/image/upload",
      {
        method: "POST",
        body: data
      }
    );
    const file = await res.json();
    this.setState({
      image: file.secure_url,
      largeImage: file.eager[0].secure_url
    });
  };

  render() {
    return (
      <Container>
        <Title>Avatar Creater</Title>
        <StyledP>
          Spread the muffin by adding one to your social media avatar.
        </StyledP>
        <form
          onSubmit={async e => {
            e.preventDefault();

            // Router.push({
            //   pathname: '/item',
            //   query: { id: res.data.createItem.id },
            // });
          }}
        >
          <input
            type="file"
            id="file"
            name="file"
            placeholder="Upload an image"
            required
            onChange={this.uploadFile}
          />
        </form>
      </Container>
    );
  }
}

export default CreateAvatarPage;
